table {
    border-collapse: collapse; /* Объединение границ ячеек таблицы */
    border: 2px solid black; /* Устанавливаем толщину и цвет рамки */
    width: 100%; /* Устанавливаем ширину таблицы */
}

/* Стиль для ячеек таблицы */
td, th {
    border: 1px solid black; /* Устанавливаем толщину и цвет рамки для ячеек */
    padding: 8px; /* Добавляем внутренний отступ для содержимого ячеек */
    text-align: left; /* Выравниваем текст в ячейках по левому краю */
}

/* Дополнительный стиль для заголовков таблицы (элементы <th>) */
th {
    background-color: #f2f2f2; /* Задаем фоновый цвет для заголовков */
}